___CSS_LOADER_EXPORT___.push([module.id, ".full-width,.my-table{width:100%}.my-table,.my-td{border:1px solid #ccc}.my-td{padding:2px}.opened{background-color:#02fffb}.row-centered{text-align:center}.col-centered{display:inline-block;float:none;margin-right:-4px;text-align:left}.pos{position:top;top:240px}.v-enter-active,.v-leave-active{transition:opacity .5s ease}.v-enter-from,.v-leave-to{opacity:0}.bounce-enter-active{animation:bounce-in .5s}.bounce-leave-active{animation:bounce-in .5s reverse}@keyframes bounce-in{0%{transform:scale(0)}50%{transform:scale(1.1)}to{transform:scale(1)}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/SummaryOfRating/SemestralRating.vue"],"names":[],"mappings":"AAuLA,sBAJI,UAOJ,CAEA,iBAHI,qBAMJ,CAHA,OACI,WAEJ,CAEA,QACI,wBACJ,CAGA,cACI,iBACJ,CAEA,cACI,oBAAqB,CACrB,UAAW,CAEX,iBAAkB,CADlB,eAEJ,CAEA,KACI,YAAa,CACb,SACJ,CAGA,gCAEI,2BACJ,CAEA,0BAEI,SACJ,CAGA,qBACI,uBACJ,CAEA,qBACI,+BACJ,CAEA,qBACI,GACI,kBACJ,CAEA,IACI,oBACJ,CAEA,GACI,kBACJ,CAGJ","sourcesContent":["<template>\n\n    <Head>\n        <title>Home</title>\n    </Head>\n    <div class=\"row gap-20 masonry pos-r\">\n        <div class=\"peers fxw-nw jc-sb ai-c\">\n            <h3>Employee's Semestral Rating - {{ Semester(sem) + \" \" + year }}</h3>\n            <div class=\"peers\">\n                <div class=\"peer mR-10\">\n\n                </div>\n                <div class=\"peer\">\n\n                    <button class=\"btn btn-primary btn-sm mL-2 text-white\" @click=\"printSubmit1\">Print Summary\n                    </button>\n\n                </div>\n                <div class=\"peer\">\n\n                </div>\n            </div>\n\n            <Link :href=\"'/summary-rating'\">\n            <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"25\" height=\"25\" fill=\"currentColor\" class=\"bi bi-x-lg\"\n                viewBox=\"0 0 16 16\">\n                <path fill-rule=\"evenodd\"\n                    d=\"M13.854 2.146a.5.5 0 0 1 0 .708l-11 11a.5.5 0 0 1-.708-.708l11-11a.5.5 0 0 1 .708 0Z\" />\n                <path fill-rule=\"evenodd\"\n                    d=\"M2.146 2.146a.5.5 0 0 0 0 .708l11 11a.5.5 0 0 0 .708-.708l-11-11a.5.5 0 0 0-.708 0Z\" />\n            </svg>\n            </Link>\n        </div>\n\n        <div class=\"masonry-sizer col-md-6\"></div>\n        <div class=\"masonry-item w-100\">\n            <div class=\"row gap-20\"></div>\n            <div class=\"bgc-white p-20 bd\">\n                <div class=\"table-responsive\">\n                    <table class=\"table table-sm table-bordered border-dark table-hover\">\n                        <thead>\n                            <tr style=\"background-color: #B7DEE8;\" class=\"text-center table-bordered\">\n                                <th style=\"width: 5%;\" rowspan=\"2\" colspan=\"1\">Full Name</th>\n                                <th style=\"width: 20%;\" colspan=\"4\">Rating</th>\n                            </tr>\n                            <tr style=\"background-color: #B7DEE8;\" class=\"text-center\">\n                                <th style=\"width: 5%;\">Numerical Rating</th>\n                                <th style=\"width: 5%;\">Adjectival Rating</th>\n                            </tr>\n                            <tr>\n\n                            </tr>\n                        </thead>\n                        <tbody>\n\n                            <template v-for=\"(dat, index) in data\" :key=\"index\">\n                                <tr>\n                                    <td>{{ dat.Fullname }}</td>\n                                    <td>{{ dat.numericalRating }}</td>\n                                    <td>{{ dat.adjectivalRating }}</td>\n                                </tr>\n\n                            </template>\n                            <tr>\n                                <td colspan=\"2\"><b style=\"float:right\">Employee's Total Average Rating</b></td>\n                                <td>{{ averageRate }}</td>\n                            </tr>\n                        </tbody>\n                    </table>\n                </div>\n\n            </div>\n        </div>\n        <Modal v-if=\"displayModal\" @close-modal-event=\"hideModal\">\n            <!-- {{ my_link }} -->\n            <div class=\"d-flex justify-content-center\">\n                <iframe :src=\"my_link\" style=\"width:100%; height:450px\" />\n            </div>\n        </Modal>\n\n\n    </div>\n</template>\n<script>\n\nimport { useForm } from \"@inertiajs/inertia-vue3\";\nimport Modal from \"@/Shared/PrintModal\";\nimport Modals from \"@/Shared/Modal\"\nexport default {\n    props: {\n        auth: Object,\n        data: Object,\n        year: String,\n        sem: String,\n        office: String,\n        pgHead: String,\n    },\n    data() {\n        return {\n            displayModal: false,\n            my_link: \"\",\n            averageRate: 0,\n            form: useForm({\n                remarks: \"\",\n                remarks_id: \"\",\n                year: \"\",\n                month: \"\",\n                idIPCR: \"\",\n                idSemestral: \"\",\n                emp_code: \"\",\n            })\n        }\n    },\n    components: {\n        Modal, Modals,\n    },\n    mounted() {\n        this.calculateAverageRate()\n    },\n\n    methods: {\n        Semester(sem) {\n            var result = \"\";\n            if (sem == \"1\") {\n                result = \"January to June\";\n            } else if (sem == \"2\") {\n                result = \"July to December\"\n            }\n\n            return result;\n        },\n        printSubmit1() {\n            this.my_link = this.viewlink1(this.year, this.month, this.office);\n            this.showModal1();\n\n            console.log(this.my_link)\n\n        },\n        viewlink1(year, month, office) {\n            var linkt = \"http://\";\n            var jasper_ip = this.jasper_ip;\n            var jasper_link = 'jasperserver/flow.html?pp=u%3DJamshasadid%7Cr%3DManager%7Co%3DEMEA%2CSales%7Cpa1%3DSweden&_flowId=viewReportFlow&_flowId=viewReportFlow&ParentFolderUri=%2Freports%2FIPCR&reportUnit=%2Freports%2FIPCR%2FMonthlyRating&standAlone=true&decorate=no&output=pdf';\n            var params = '&year=' + year + '&month=' + month + '&department_code=' + office;\n            var linkl = linkt + jasper_ip + jasper_link + params;\n\n            return linkl;\n        },\n        showModal1() {\n            this.displayModal = true;\n        },\n        hideModal() {\n            this.displayModal = false;\n        },\n        calculateAverageRate() {\n            let sum = 0;\n            let num_of_data = 0;\n            let average = 0;\n\n            if (Array.isArray(this.data)) {\n                this.data.forEach(item => {\n                    if (item.numericalRating !== 0) {\n                        num_of_data += 1;\n                        sum += parseFloat(item.numericalRating);\n                        average = sum / num_of_data;\n                    }\n                    // console.log(num_of_data);\n                });\n            }\n            this.averageRate = average.toFixed(2);\n            // console.log(this.averageRate)\n            return this.averageRate;\n        },\n\n    }\n};\n</script>\n<style>\n/***TABLE FULL WIDTH */\n.full-width {\n    width: 100%;\n}\n\n/**ACCORDION BEGIN*********************/\n.my-table {\n    width: 100%;\n    border: 1px solid #ccc;\n}\n\n.my-td {\n    padding: 2px;\n    border: 1px solid #ccc;\n}\n\n.opened {\n    background-color: rgb(2, 255, 251);\n}\n\n/**ACCORDION END*********************/\n.row-centered {\n    text-align: center;\n}\n\n.col-centered {\n    display: inline-block;\n    float: none;\n    text-align: left;\n    margin-right: -4px;\n}\n\n.pos {\n    position: top;\n    top: 240px;\n}\n\n/*TOGGLE FADE TRANSITION*/\n.v-enter-active,\n.v-leave-active {\n    transition: opacity 0.5s ease;\n}\n\n.v-enter-from,\n.v-leave-to {\n    opacity: 0;\n}\n\n/* transition */\n.bounce-enter-active {\n    animation: bounce-in 0.5s;\n}\n\n.bounce-leave-active {\n    animation: bounce-in 0.5s reverse;\n}\n\n@keyframes bounce-in {\n    0% {\n        transform: scale(0);\n    }\n\n    50% {\n        transform: scale(1.1);\n    }\n\n    100% {\n        transform: scale(1);\n    }\n\n\n}\n</style>\n"],"sourceRoot":""}]);
